/* reset */
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    outline: none;
}

ul,
ol,
li {
    list-style: none;
}

a {
    color: inherit;
    cursor: pointer;
    text-decoration: none;
}

button {
    border: 0;
    cursor: pointer;
    font-family: inherit;
}

body {
    line-height: 1.6;
    font-size: 16px;
    font-family: 
    'Spoqa Han Sans','Noto Sans KR', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;

    
}

.inner {
    max-width: 1140px;
    margin:  0 auto;
}
